<template>
<div>
<div id="container-main" class="full-screen">
  <div id="container-recipe">
    <div id="exit" @click.prevent="handleZoom">
      <b>X</b>
    </div>
    <h1>{{ selectedRecipe.Title }}</h1>
    <div id="container-details">
      <div id="ingredients">
        <ul>
          <li
            v-for="ingredient in selectedRecipe.Ingredients"
            :key="ingredient.Name"
          >
            {{ ingredient.Name }}
          </li>
        </ul>
      <button @click.prevent="handleAdd">Add to shopping list</button>
      </div>
      <div id="instructions">
        <p>{{ selectedRecipe.Instructions }}</p>
      </div>
    </div>
  </div>
  <span id='click-me' class="full-screen" @click.prevent="handleZoom"></span>
</div>
</div>
</template>

<script>
// import './main.css';
import { addToShoppingList } from '../../services/api.js';
export default {
  props: {
    toggleRecipe: {
      type: Function,
      require: true
    },
    selectedRecipe: Object,
    addToMasterList: {
      type: Function,
      required: true
    }
  },
  data() {
    return {
      newUser: true,
      creds: { username : '', password : '' },
      label: null,
      show: false,
      valid: false
    };
  },
  methods: {
    handleZoom() {
      this.toggleRecipe();
    },
    handleAdd() {
      let ingredients = {};
      ingredients = this.selectedRecipe.Ingredients.reduce((acc, cur, i) => {
        acc[i] = { name : cur.Name, selected : false, userid : 1 }; // Replace with localstorage userid
        return acc;
      }, []);

      // let i = { userid : 1, selected : false, name : 'bananas' };
      console.log('\n\nasdklfjas', ingredients, typeof(ingredients));
      addToShoppingList(ingredients)
        .then(result => {
          console.log('\n\n result is', result);
          if(result.added) {
            this.addToMasterList(ingredients);
          }
        });
    }
  }
};
</script>
